



































































































































































































































































































h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.container {
  max-width: 100%;
  height: 100%;
}
.songStyle__container {
  width: 100%;
  padding: 0 15px;
}
.main_title {
  text-align: center;
}

.styles__container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0 auto;
  flex-wrap: wrap;
}
.style__tag {
  padding: 5px;
  border: 1px solid green;
  border-radius: 5px;
  margin: 5px;
}
.input {
  max-width: 300px;
  margin: 20px auto;
}
.verse {
  margin: 40px 0;
}
.chorus {
  color: green;
}
